import React, {Component} from "react";

import Button from '../../commonComponents/ButtonComponent/Button';
import Thumbnail from "../../commonComponents/ThumbnailComponent/Thumbnail";

import {formatDate} from "../../libs/helpers";
import {MAX_DESCRIPTION_LENGTH} from '../../libs/constant';

import './CollectionItem.scss';

class CollectionItem extends Component {

  changeFavorite = (id) => {
    this.props.toggleFavorite(id);
  };

  removeItem = (id) => {
    this.props.removeItem(id);
  };

  editItem = (id) => {
    this.props.editItem(id);
  };

  onAddItem = (data) => {
    this.props.onAddItem(data);
  };

  render() {
    const {data, isAddNew} = this.props;

    return (
      <>
        {
          data && <div className="item">
            <div className="item__media">
              <a href="#">
                {
                  data.preview && <Thumbnail type={data.mediaType} source={data.preview}></Thumbnail>
                }
              </a>
            </div>
            {/* <p className="item__author">

        </p> */}
            <div className="item__date">
              <span>{formatDate(data.createdAt)}</span>
            </div>
            <h3 className="item__title">{data.title}</h3>
            <div className="item__description">
              {data.description.length > MAX_DESCRIPTION_LENGTH ? `${data.description.substr(0, MAX_DESCRIPTION_LENGTH).trim()}...` : data.description}
            </div>

            <div className="item__buttons">
              {
                isAddNew ?
                  <Button props={{title: "Add to collection", icon: 'add', type: "default", fullWidth: true}}
                          onClick={() => this.onAddItem(data)}/>
                  :
                  <>
                    <Button
                      props={{title: "", icon: data.favorite ? 'heart-filled' : 'heart', type: "default"}}
                      onClick={() => this.changeFavorite(data.id)}/>
                    <Button
                      props={{title: "", icon: "trash-bin", type: "default"}}
                      onClick={() => this.removeItem(data.id)}/>
                    <Button
                      props={{icon: "pen", type: "default"}}
                      onClick={() => this.editItem(data.id)}/>
                  </>
              }
            </div>
          </div>
        }
      </>
    )
  }
}

export default CollectionItem;