import React, {Component} from 'react';
import {connect} from 'react-redux';
// import moment from 'moment';

import Input from '../../commonComponents/InputComponent/Input';
import Dropdown from '../../commonComponents/InputComponent/Dropdown';

import { updateFilter } from '../../actions/filterSort';
import { typeOptions, favoriteOptions, sortOptions } from '../../libs/constant';

class FilterContainer extends Component {
  state = {
    type: '',
    date: '',
    favorite: '',
    sort: '',
  };

  onChangeValue = (name, value) => {
    const { updateFilter } = this.props;

    this.setState({
      [name]: value,
    });
    updateFilter(name, value);
  };

  render() {
    const {
      type,
      date,
      favorite,
      sort,
    } = this.state;
    return (
      <div className="row">
        <div className="col-sm-6 col-md-3">
          <Dropdown options={typeOptions} label="Type" name="type"
                    onChangeData={this.onChangeValue} value={type}/>
        </div>
        <div className="col-sm-6 col-md-3">
          <Input type="date" label="Date" name="date" title="Date"
                 onChangeData={this.onChangeValue} value={date}/>
        </div>
        <div className="col-sm-6 col-md-3">
          <Dropdown options={favoriteOptions} label="Favorite" name="favorite"
                    onChangeData={this.onChangeValue} value={favorite}/>
        </div>
        <div className="col-sm-6 col-md-3">
          <Dropdown options={sortOptions} label="Sort By" name="sort"
                    onChangeData={this.onChangeValue} value={sort}/>
        </div>
      </div>
    )
  }
}

const mapStateToProps = (
  {
    collection,
    filter,
  }) => ({
  collection,
  filter,
});

const mapDispatchToProps = (dispatch) => ({
  updateFilter: (name, value) => dispatch(updateFilter(name, value)),
});

export default connect(
  mapStateToProps,
  mapDispatchToProps,
)(FilterContainer);