import {divMod} from "./helpers";
import {ITEM_PER_PAGE_SEARCH, MAX_TOTAL_PAGE, DEFAULT_IMAGE_PREVIEW} from './constant';

export async function searchNasa(query, page = 1) {
  if (!query) {
    return {items: []};
  }

  const res = await fetch(`https://images-api.nasa.gov/search?q=${encodeURIComponent(query)}${page < 2 ? '' : `&page=${page}`}`);
  const json = await res.json();

  return {
    items: json.collection.items.map(({href, data: [{nasa_id, title, media_type, date_created, description}], links}) => ({
      id: nasa_id,
      href,
      title,
      description,
      mediaType: media_type,
      preview: (links && links.find((x) => x.rel === 'preview').href) || DEFAULT_IMAGE_PREVIEW,
      createdAt: date_created,
    })),
    totalHits: json.collection.metadata.total_hits,
  }
}

export class NasaSearchPagingAdapter {
  _items = {};
  _size = ITEM_PER_PAGE_SEARCH;

  constructor(query) {
    this._query = query;
  }

  async getPage(page = 1) {
    const {
      start: pageStart,
      pages: pageNeeded,
    } = NasaSearchPagingAdapter.getPageNeeded(page, this._size);

    for (let p of pageNeeded) {
      if (this._items[p]) {
        continue;
      }

      const {items, totalHits} = this._count && p > (this._count / 100 + 1) ? [] : await searchNasa(this._query, p);

      this._count = this._count || Math.min(totalHits, MAX_TOTAL_PAGE);
      this._items[p] = items;
    }

    return {
      items: pageNeeded.reduce((a, p) => [...a, ...this._items[p]], []).slice(pageStart, pageStart + this._size),
      totalHits: this._count,
    };
  }

  static getPageNeeded(page, size, nasaSize = 100) {
    const [nasaPage, nasaPageStart] = divMod((page - 1) * size, nasaSize);
    const pages = [nasaPage + 1];

    if (nasaPageStart > nasaSize - size) {
      pages.push(nasaPage + 2);
    }

    return {
      start: nasaPageStart,
      pages,
    };
  }
}
