import React, { Component } from 'react';
import classNames from 'classnames';

import './Input.scss';

class Input extends Component {
  state = {
    filled: this.props.defaultValue || this.props.value,
    textInput: React.createRef(),
    value: this.props.defaultValue || this.props.value,
    inValid: require && !(this.props.defaultValue || this.props.value),
  };

  onChange = (e) => {
    const { filled } = this.state;
    const inputValue = e.target.value;
    const { required } = this.props;

    const inValid = required ? (inputValue === ''): false;
    if (!filled && inputValue) {
      this.setState({
        filled: true,
        value: inputValue,
        inValid,
      });
    } else if (inputValue === '') {
      this.setState({
        filled: false,
        value: inputValue,
        inValid,
      });
    }
    this.props.onChangeData && this.props.onChangeData(e.target.name, inputValue, inValid);
  };

  onKeyPress = (e) => {
    if (e.which === 13 || e.keyCode === 13) {
      this.props.onChange && this.props.onChange(e.target.value);
    }
  };

  render() {
    const { type, defaultValue, label, placeHolder, name, value, required, showInvalidField } = this.props;
    const { inValid, textInput } = this.state;

    const inputClass = {
      filled: this.state.filled || type === 'date',
      'form-element__input': true,
      'form-element__input--textarea': type === 'textarea',
      'form-element__input--invalid': showInvalidField && inValid,
    };
    return (
      <div className="form-element">
        {
          type === 'input'
          && <input className={classNames(inputClass)}
            type="text"
            defaultValue={defaultValue}
            placeholder={(!label && placeHolder) ? placeHolder : null}
            name={name}
            value={value}
            onChange={(e) => this.onChange(e)}
            onKeyPress={(e) => this.onKeyPress(e)}
            ref={this.state.textInput} />
        }
        {
          type === 'textarea'
          && <textarea
            className={classNames(inputClass)}
            name={name} type="text"
            defaultValue={defaultValue}
            value={value}
            onChange={(e) => this.onChange(e)}>
          </textarea>
        }

        {
          type === 'date'
          && <input className={classNames(inputClass)}
            type="date"
            defaultValue={defaultValue}
            placeholder={(!label && placeHolder) ? placeHolder : null}
            name={name}
            value={value}
            onChange={(e) => this.onChange(e)}
            onKeyPress={(e) => this.onKeyPress(e)}
            ref={textInput} />
        }

        {
          label && <label className="form-element__label"
            onClick={() => textInput.current.focus()}>
            {label}
            {
              required && <span className='required'>*</span>
            }
            </label>
        }

      </div>
    )
  }
}

export default Input;

