import React, {Component} from 'react';
import {Link} from 'react-router-dom';
import {connect} from 'react-redux';
import {withRouter} from 'react-router-dom';

import Input from '../../commonComponents/InputComponent/Input';
import Button from '../../commonComponents/ButtonComponent/Button';
import CollectionItem from '../../components/CollectionItemComponent/CollectionItem';
import Modal from '../../components/ModalComponent/Modal';
import Paging from '../../commonComponents/PagingComponent/Paging';
import AlertNotify from "../../commonComponents/AlertNotify/AlertNotify";

import {NasaSearchPagingAdapter} from '../../libs/api';
import {generatePaging, getLastPage} from "../../libs/helpers";
import {
  ITEM_PER_PAGE_SEARCH, MESSAGE_ADD_FAIL,
  MESSAGE_ADD_SUCCESS,
} from '../../libs/constant';
import {updateCollectionItem} from '../../actions/collection';

import './Search.scss';

class SearchContainer extends Component {

  state = {
    resultItems: [],
    collectionItemToAdd: null,
    searching: false,
    searchValue: '',
    totalHits: '',
    isOpen: false,
    pages: [],
    currentPage: 0,
    inValidField: false,
    showInvalidField: false,
    show: false,
    status: 'success',
  };

  searchData = async (value) => {
    if (!value) {
      this.setState({
        searchValue: value,
      });
      return;
    }
    try {
      this.setState({
        searching: true,
        searchValue: value,
      });
      this.nasaSearchAdapter = new NasaSearchPagingAdapter(value);

      await this.gotoPage(1);
    } catch (err) {
      console.log('Search false ', err);
    }
  };

  gotoPage = async (page) => {
    try {
      const {items, totalHits} = await this.nasaSearchAdapter.getPage(page);

      const paging = generatePaging(totalHits, ITEM_PER_PAGE_SEARCH, page);

      this.setState({
        searching: false,
        resultItems: items,
        pages: paging,
        currentPage: page,
        totalHits,
      });
    } catch (e) {
      alert(`ERROR: ${e.message}`);
    }
  };

  editBeforeAddItem = (data) => {
    this.setState({
      collectionItemToAdd: data,
      isOpen: true,
    });
  };

  onCloseModal = () => {
    this.setState({
      isOpen: false,
      collectionItemToAdd: null,
    })
  };

  addCollectionItem = () => {
    const {collectionItemToAdd, inValidField} = this.state;
    const {addItemToCollection} = this.props;

    if (!inValidField) {
      addItemToCollection(collectionItemToAdd);

      this.setState({
        isOpen: false,
        collectionItemToAdd: null,
        showInvalidField: false,
        show: true,
        status: 'success',
      });
      this.hideShowAlert();
    } else {
      this.setState({
        showInvalidField: inValidField,
      });
    }

  };

  hideShowAlert = () => {
    setTimeout(() => {
      this.setState({
        show: false,
        success: '',
      });
    }, 3000);
  };

  onChangeValue = (name, value, inValid = false) => {
    let collectionItemToAdd = {...this.state.collectionItemToAdd};
    collectionItemToAdd[name] = value;
    this.setState({collectionItemToAdd, inValidField: inValid,});
  };

  render() {
    const {
      resultItems,
      searching,
      searchValue,
      isOpen,
      totalHits,
      pages,
      currentPage,
      collectionItemToAdd,
      showInvalidField,
      status,
      show,
    } = this.state;

    const lastPage = getLastPage(totalHits, ITEM_PER_PAGE_SEARCH);

    const alertNotifyInfo = {
      status,
      show,
      title: (status === 'success' && MESSAGE_ADD_SUCCESS) || ((status === 'fail') && MESSAGE_ADD_FAIL),
    };

    return (
      <>
        <div className="grid">
          <div className="search">
            <div className="search__header">
              <Link to="/">
                <Button props={{icon: "back", title: "Back to Collection", type: "primary-color"}}/>
              </Link>
              <div className="search__title">Search From NASA</div>
              <div className="search__input">
                <Input type="input" placeHolder="Type something to search..." onChange={this.searchData}/>
              </div>
            </div>
            <div className="search-result">
              {
                searching && <p className="search-result__searching">Searching...</p>
              }
              {
                totalHits !== '' && !searching &&
                <p className="search-result__searching">{totalHits} result for "{searchValue}"</p>
              }
              {
                totalHits ? <Paging total={totalHits} pages={pages} currentPage={currentPage}
                                    lastPage={lastPage}
                                    itemPerPage={ITEM_PER_PAGE_SEARCH}
                                    gotoPage={this.gotoPage}/> : ''
              }
              <div className="row">
                {
                  resultItems && resultItems.map((item) => <div key={item.id} className="col-md-4">
                    <CollectionItem data={item} onAddItem={this.editBeforeAddItem} isAddNew={true}/>
                  </div>)
                }
              </div>
            </div>
          </div>
        </div>
        <Modal
          onCloseModal={this.onCloseModal}
          isOpen={isOpen}
          data={collectionItemToAdd}
          saveData={this.addCollectionItem}
          onChangeData={this.onChangeValue}
          modalTitle="Add to collection"
          buttonInfo={{icon: 'check', title: 'Add to collection', type: 'primary'}}
          showInvalidField={showInvalidField}
        />
        <AlertNotify alertInfo={alertNotifyInfo}/>
      </>
    )
  }
}

const mapStateToProps = (
  {
    collection,
  }) => ({
  collection,
});

const mapDispatchToProps = (dispatch) => ({
  addItemToCollection: (item) => dispatch(updateCollectionItem(item.id, item)),
});

export default withRouter(connect(
  mapStateToProps,
  mapDispatchToProps,
)(SearchContainer));
