import {
  UPDATE_FILTER_SORT,
  PAGE_FIRST, PAGE_LAST,
  PAGE_GOTO,
  PAGE_PREVIOUS, PAGE_NEXT,
  PAGE_SIZE,
} from '../actions/types';

import { ITEM_PER_PAGE } from '../libs/constant';

const DEFAULT_PAGING = {
  currentPage: 1,
  size: ITEM_PER_PAGE,
};

export default (paging = DEFAULT_PAGING, action) => {
  switch (action.type) {
    case PAGE_GOTO:
      return {
        ...paging,
        currentPage: action.currentPage,
      };
    case PAGE_NEXT:
      return {
        ...paging,
        currentPage: paging.currentPage + 1,
      };
    case PAGE_PREVIOUS:
      return paging.currentPage > 1 ? {
        ...paging,
        currentPage: paging.currentPage - 1,
      } : paging;
    case UPDATE_FILTER_SORT:
    case PAGE_FIRST:
      return {
        ...paging,
        currentPage: 1,
      };
    case PAGE_LAST:
      return {
        ...paging,
        currentPage: 0, // TODO go to last page using Redux
      };
    case PAGE_SIZE:
      return {
        ...paging,
        size: action.size,
      };
    default:
      return paging;
  }
}
