import React, {Component} from 'react';
import classNames from 'classnames';

import './Paging.scss';

class Paging extends Component {

  gotoPage = (page) => {
    this.props.gotoPage(page);
  };

  render() {
    const {
      total,
      currentPage,
      pages = [],
      lastPage,
      itemPerPage = 0,
    } = this.props;

    const firstShowing = (currentPage - 1) * itemPerPage + 1;
    const lastShowing = Math.min((firstShowing + itemPerPage - 1), total);

    const pagingClass = {
      pagination: true,
      'justify-content-end': total > 0,
    }
    return (
      <div className={classNames(pagingClass)}>
        <div className="pagination__wrapper">
          {
            total ? <>
                <p className="pagination__desc">Showing item from {firstShowing} - {lastShowing} of {total} item/s</p>
                <ul className="pagination__number">
                  {
                    currentPage !== 1 && <>
                      <li className="pagination__item">
                        <a className="pagination__link" aria-label="First" href='javascript:void(0)'
                           onClick={() => this.gotoPage(1)}
                        > {'<<'}</a>
                      </li>
                      <li className="pagination__item">
                        <a className="pagination__link" aria-label="Previous" href='javascript:void(0)'
                           onClick={() => this.gotoPage(currentPage - 1)}>{'<'}</a>
                      </li>
                    </>
                  }
                  {
                    pages && pages.map((page, index) => <li
                      className={classNames({'pagination__item': true, active: page === currentPage})}
                      key={index}>
                      <a className="pagination__link" href='javascript:void(0)'
                         disabled={page === currentPage}
                         onClick={() => this.gotoPage(page)}>
                        {page}</a>
                    </li>)
                  }
                  {
                    (currentPage !== lastPage) && <>
                      <li>
                        <a className="pagination__link" href='javascript:void(0)'
                           onClick={() => this.gotoPage(currentPage + 1)}>{'>'}</a>
                      </li>
                      <li>
                        <a className="pagination__link" href='javascript:void(0)'
                           onClick={() => this.gotoPage(lastPage)}>{'>>'}</a>
                      </li>
                    </>
                  }
                </ul>
              </>
              : <p className="pagination__desc">There no item to show</p>
          }
        </div>
      </div>
    )
  }
}

export default Paging;