import {TOGGLE_FAVORITE_COLLECTION} from "../actions/types";

const collectionItem = (collectionItem = {

}, action) => {
  switch (action.type) {
    case TOGGLE_FAVORITE_COLLECTION:
      return {
        ...collectionItem,
        favorite: !collectionItem.favorite,
      };
    default:
      return collectionItem;
  }
};

export default collectionItem;