import React, { Component } from 'react';
import classNames from 'classnames';

import './Input.scss';

class Dropdown extends Component {
  state = {
    filled: false,
    value: this.props.defaultValue || '',
    title: '',
  };

  onChangeOption = (option) => {
    const { value } = this.state;
    if(option.value !== value){
      this.setState({
        value: option.value,
        title: option.label,
      });

      const { name } = this.props;
      this.props.onChangeData && this.props.onChangeData(name, option.value);
    }
  };

  componentDidMount(){
    const { value } = this.state;
    const { options } = this.props;

    const selectedOption = options.find((option) => option.value === value);
    selectedOption && this.setState({title : selectedOption.label});
  }
  render() {
    const { options, label,  name } = this.props;
    const { value, title } = this.state;
    const inputClass = {
      filled: true,
      'form-element__input': true,
    };

    return (
      <div className="form-element">
        <input
          className="hidden"
          type="text"
          name={name}
          value={value}
          onChange={(e) => this.onChange(e)}/>
        <button className={classNames(inputClass)}>{title}</button>
        <ul className="form-element__dropdown">
          {
            options.map((option, index) => 
              <li key={index}
                className={classNames({active: value === option.value})}
                onMouseDown={() => this.onChangeOption(option)}>{option.label}</li>
            )}
        </ul>
        <span className="icon-dropdown"></span>
        <label className="form-element__label">{label}</label>

      </div>
    )
  }
}

export default Dropdown;

