import React, { Component } from 'react';
import { connect } from 'react-redux';
import {
  BrowserRouter,
  Route
  } from 'react-router-dom';

import CollectionContainer from './containers/CollectionContainer/CollectionContainer';
import SearchContainer from './containers/SearchCollectionContainer/SearchContainer';

import './App.scss';


class App extends Component {
  render() {
    return (
      <div className="container">
        <BrowserRouter basename="/nfq-test">
          <Route exact path="/" component={CollectionContainer} />
          <Route path="/search-collection" component={SearchContainer}/>
        </BrowserRouter>
      </div>
    );
  }
}

const mapStateToProps = () => ({
})

export default connect(
  mapStateToProps,
)(App);
