import React, { Component } from 'react';
import classNames from 'classnames';

import Button from '../../commonComponents/ButtonComponent/Button';

import './Modal.scss';

class ModalConfirm extends Component {

  onCloseModal = () => {
    this.props.onCloseModal();
  };

  removeItem = () => {
    this.props.removeItem();
  };

  render() {
    const modalClass = {
      'modal': true,
      'modal-open': this.props.isOpen
    };
    return (
      <div className={classNames(modalClass)} tabIndex="-1" role="dialog">
        <div className="modal-dialog modal-sm" role="document">
          <div className="modal-content">
            <div className="modal-header">
              {/* <h5 className="modal-title">Delete item</h5> */}
              <Button props={{icon:"close", type:"outline", title:""}}
                      onClick={() => this.onCloseModal()}/>
            </div>
            <div className="modal-body">
              <h4 className="modal-body__title">Do you want to remove this item?</h4>
            </div>
            <div className="modal-footer">
              <Button props={{type:"danger", title:"Remove"}}
                      onClick={this.removeItem}/>
              <Button props={{type:"default", title:"Cancel"}}
                      onClick={this.onCloseModal}/>
            </div>
          </div>
        </div>
      </div>
    )
  }
}
export default ModalConfirm;