import {
  UPDATE_COLLECTION,
  TOGGLE_FAVORITE_COLLECTION,
  DELETE_COLLECTION_ITEM,
} from './types';

export function updateCollectionItem(id, item) {
  return {
    type: UPDATE_COLLECTION,
    id,
    item,
  };
}

export function toggleFavoriteCollectionItem(id) {
  return {
    type: TOGGLE_FAVORITE_COLLECTION,
    id,
  };
}

export function removeCollectionItem(id) {
  return {
    type: DELETE_COLLECTION_ITEM,
    id,
  };
}
