import moment from 'moment';

import { mediaType, favoriteType, sortBy } from './constant';

export function formatDate(date) {
  return moment(date).format('ll');
}

export function debounce(func, delay) {
  let handler;
  return function (...args) {
    clearTimeout(handler);
    handler = setTimeout(() => func(...args), delay);
  }
}

export function getCollection(collection, paging, filter) {
  collection = Object.keys(collection).map(k => collection[k]);
  if (filter) {
    if (filter.date) {
      const filterDate = moment(filter.date);
      collection = collection.filter((item) => filterDate.isSame(item.createdAt, 'day'));
    }

    switch (filter.type) {
      case mediaType.VIDEO:
        collection = collection.filter((item) => item.mediaType === mediaType.VIDEO);
        break;
      case mediaType.IMAGE:
        collection = collection.filter((item) => item.mediaType === mediaType.IMAGE);
        break;
      case mediaType.AUDIO:
        collection = collection.filter((item) => item.mediaType === mediaType.AUDIO);
        break;
      default:
        break;
    }

    switch (filter.favorite) {
      case favoriteType.FAVORITE:
        collection = collection.filter((item) => item.favorite);
        break;
      case favoriteType.UN_FAVORITE:
        collection = collection.filter((item) => !item.favorite);
        break;
      default:
        break;
    }
  }

  const total = collection.length;

    switch (filter.sort) {
      case sortBy.TITLE:
        collection.sort((a, b) => a.title.toLowerCase().localeCompare(b.title.toLowerCase()));
        break;
      case sortBy.DATE:
        collection.sort((a, b) => a.createdAt.toLowerCase().localeCompare(b.createdAt.toLowerCase()));
        break;
      default:
        break;
    }

  if (paging) {
    collection = collection.slice((paging.currentPage - 1) * paging.size, paging.currentPage * paging.size);
  }
  return {
    collection,
    total,
  };
}

export function arrayFromTo(n) {
  return Array.apply(null, {length: n}).map(Number.call, Number)
}

export function generatePaging(total, size, page, max = 7) {
  if (total === 0) {
    return [1];
  }

  const pages = arrayFromTo(Math.ceil(total / size)).map(x => x + 1);
  const lastPage = pages.length + 1;

  const after = Math.floor(max / 2);
  const before = max - after;

  if (page < before) {
    return pages.slice(0, max);
  }

  if (page > lastPage - after) {
    return pages.slice(lastPage - max, lastPage);
  }

  return pages.slice(page - before, page + after);
}

export function divMod(a, b) {
  return [~~(a / b), a % b];
}

export function getLastPage(total, itemPerPage) {
  return Math.ceil(total / itemPerPage);
}