import {
  UPDATE_COLLECTION,
  TOGGLE_FAVORITE_COLLECTION,
  DELETE_COLLECTION_ITEM,
} from '../actions/types';

import collectionReducer from './CollectionItem';

const collection = (collection = {}, action) => {
  switch (action.type) {
    case UPDATE_COLLECTION:
      return {
        ...collection,
        [action.id]: action.item
      };
    case TOGGLE_FAVORITE_COLLECTION:
      return {
        ...collection,
        [action.id]: collectionReducer(collection[action.id], action),
      };
    case DELETE_COLLECTION_ITEM:
      let newCollection = {...collection};
      delete newCollection[action.id];

      return newCollection;
    default:
      return collection;
  }
};

export default collection;
