import  React, { Component } from 'react';
import classNames from 'classnames';

import './AlertNotify.scss';

class AlertNotify extends Component{

  render(){
    const { alertInfo } = this.props;
    const classAlert = {
      alert: true,
      'alert--success': alertInfo.status === 'success',
      'alert--fail': alertInfo.status === 'fail',
      'alert--show': alertInfo.show,
    };

    return (
      <div className={classNames(classAlert)} role="alert">
        {alertInfo.title}
      </div>
    )
  }
}

export default AlertNotify;