import {PAGE_FIRST, PAGE_GOTO, PAGE_LAST, PAGE_NEXT, PAGE_PREVIOUS, PAGE_SIZE} from './types';

export function gotoPage(page) {
  return {
    type: PAGE_GOTO,
    currentPage: page,
  };
}

export function nextPage() {
  return {
    type: PAGE_NEXT,
  };
}

export function prevPage() {
  return {
    type: PAGE_PREVIOUS,
  };
}

export function firstPage() {
  return {
    type: PAGE_FIRST,
  };
}

export function lastPage() {
  return {
    type: PAGE_LAST,
  };
}

export function setPageSize(size) {
  return {
    type: PAGE_SIZE,
    size,
  };
}
