import { UPDATE_FILTER_SORT } from '../actions/types';
const filter = (
  filterData = {
    title: '',
    date: '',
    favorite: '',
    sort: '',
  }, action) => {
  switch (action.type) {
    case UPDATE_FILTER_SORT:
      return {
        ...filterData,
        [action.name]: action.value,
      };
    default:
      return filterData;
  }
};

export default filter;