import React, {Component} from 'react';
import classNames from 'classnames';

import Input from '../../commonComponents/InputComponent/Input';
import Dropdown from '../../commonComponents/InputComponent/Dropdown';

import './Modal.scss';
import Button from '../../commonComponents/ButtonComponent/Button';

const options = [
  {
    label: 'Video',
    value: 'video',
  }, {
    label: 'Image',
    value: 'image',
  }, {
    label: 'Audio',
    value: 'audio',
  }
];

class Modal extends Component {

  onCloseModal = () => {
    this.props.onCloseModal(false);
  };

  saveData = () => {
    this.props.saveData();
  };

  onChangeValue = (name, value, inValid = false) => {
    this.props.onChangeData(name, value, inValid);
  };

  render() {
    const modalClass = {
      'modal': true,
      'modal-open': this.props.isOpen
    };

    const {data, modalTitle, buttonInfo, showInvalidField,} = this.props;

    return (
      <div className={classNames(modalClass)} tabIndex="-1" role="dialog">
        {
          data && <div className="modal-dialog modal-dialog--lg" role="document">
            <div className="modal-content">
              <div className="modal-header">
                <h5 className="modal-title">{modalTitle}</h5>
                <Button props={{icon: "close", type: "outline"}}
                        onClick={() => this.onCloseModal()}/>
              </div>
              <div className="modal-body">
                <Input type="input" defaultValue={data.title} label="Title" name="title"
                       onChangeData={this.onChangeValue}/>
                <Input type="textarea" defaultValue={data.description} label="Description" name="description"
                       onChangeData={this.onChangeValue}/>
                <Dropdown defaultValue={data.mediaType} label="Type" options={options} name="mediaType"
                          onChangeData={this.onChangeValue}/>
                <Input type="input" defaultValue={data.preview} required={true}
                       label="Link preview image url" name="preview"
                       showInvalidField={showInvalidField}
                       onChangeData={this.onChangeValue}/>
                <Input type="input" defaultValue={data.href} required={true}
                       label="Link title url" name="href"
                       showInvalidField={showInvalidField}
                       onChangeData={this.onChangeValue}/>

              </div>
              <div className="modal-footer">
                <Button props={buttonInfo}
                        onClick={this.saveData}/>
              </div>
            </div>
          </div>
        }
      </div>
    )
  }
}

export default Modal;