import React, {Component} from 'react';

import {
  Link,
} from 'react-router-dom';
import {connect} from 'react-redux';

import CollectionItem from '../../components/CollectionItemComponent/CollectionItem';
import Button from '../../commonComponents/ButtonComponent/Button';
import FilterContainer from '../FilterContainer/FilterContainer';
import Modal from '../../components/ModalComponent/Modal';
import ConfirmModal from '../../components/ModalComponent/ConfirmModal';
import Paging from "../../commonComponents/PagingComponent/Paging";
import AlertNotify from "../../commonComponents/AlertNotify/AlertNotify";

import {
  toggleFavoriteCollectionItem,
  removeCollectionItem,
  updateCollectionItem
} from '../../actions/collection';

import {
  gotoPage,
} from '../../actions/paging';

import {
  getCollection,
  generatePaging, getLastPage,
} from "../../libs/helpers";

import {
  ITEM_PER_PAGE,
  MESSAGE_UPDATE_SUCCESS,
  MESSAGE_DELETE_SUCCESS,
} from "../../libs/constant";

import './Collection.scss';


class CollectionContainer extends Component {
  state = {
    isOpenConfirm: false,
    isOpen: false,
    itemToUpdate: null,
    itemIdToRemove: null,
    inValidField: false,
    showInvalidField: false,
    show: false,
    status: '',
  };

  showConfirmToRemoveItem = (id) => {
    this.setState({
      isOpenConfirm: true,
      itemIdToRemove: id,
    })
  };

  removeItem = () => {
    const {removeItem} = this.props;
    const {itemIdToRemove} = this.state;

    this.setState({
      isOpenConfirm: false,
      itemIdToRemove: null,
      status: 'success',
      show: true,
      title: MESSAGE_DELETE_SUCCESS,
    });
    removeItem(itemIdToRemove);
    this.hideShowAlert();
  };

  editItemBeforeUpdate = (item) => {
    this.setState({
      isOpen: true,
      itemToUpdate: {...item},
    });
  };

  onCloseModal = () => {
    this.setState({
      isOpen: false,
      itemToUpdate: null,
    })
  };

  onCloseConfirmModal = () => {
    this.setState({
      isOpenConfirm: false,
      itemIdToRemove: null,
    });
  };

  updateCollectionItem = () => {
    const {updateItemToCollection} = this.props;
    const {itemToUpdate, inValidField} = this.state;

    if (!inValidField) {
      updateItemToCollection({...itemToUpdate});
      this.setState({
        isOpen: false,
        itemToUpdate: null,
        showInvalidField: false,
        show: true,
        status: 'success',
        title: MESSAGE_UPDATE_SUCCESS,
      });
      this.hideShowAlert();
    } else {
      this.setState({
        showInvalidField: inValidField,
      });
    }
  };

  hideShowAlert = () => {
    const timeout = setTimeout(() => {
      this.setState({
        show: false,
        success: '',
      });
      clearTimeout(timeout);
    }, 3000);
  };

  onChangeValue = (name, value, inValid = false) => {
    const {itemToUpdate} = this.state;
    this.setState({
      itemToUpdate: {
        ...itemToUpdate,
        [name]: value,
      },
      inValidField: inValid,
    });
  };

  render() {
    const {
      collection,
      toggleFavorite,
      total,
      paging: {
        currentPage,
        size,
      },
      gotoPage,
    } = this.props;

    const {
      isOpen,
      isOpenConfirm,
      itemToUpdate,
      showInvalidField,
      status,
      show,
      title,
    } = this.state;

    const paging = generatePaging(total, size, currentPage);
    const lastPage = getLastPage(total, ITEM_PER_PAGE);

    const alertNotifyInfo = {
      status,
      show,
      title: title,
    };

    return (
      <>
        <div className="collection">
          <div className="grid">
            <div className="collection__header">
              <div className="row">
                <div className="col-sm-6">
                  <div className="collection__title">NASA Collection</div>
                </div>
                <div className="col-sm-6">
                  <Link to="/search-collection">
                    <Button props={{icon: "add", type: "primary", title: "Add new item"}}/>
                  </Link>
                </div>
              </div>
            </div>
            <div className="collection__filter">
              <FilterContainer/>
            </div>
            <Paging total={total} pages={paging} currentPage={currentPage}
                    lastPage={lastPage}
                    itemPerPage={ITEM_PER_PAGE}
                    gotoPage={gotoPage}/>
            <div className="row">
              {
                collection
                && collection.map((collectionItem) =>
                  <div key={collectionItem.id} className="col-md-4">
                    <CollectionItem data={collectionItem}
                                    toggleFavorite={toggleFavorite}
                                    removeItem={this.showConfirmToRemoveItem}
                                    editItem={() => this.editItemBeforeUpdate(collectionItem)}/>
                  </div>
                )
              }
            </div>
          </div>
        </div>
        <Modal
          onCloseModal={this.onCloseModal}
          isOpen={isOpen}
          data={itemToUpdate}
          saveData={this.updateCollectionItem}
          onChangeData={this.onChangeValue}
          modalTitle="Edit"
          buttonInfo={{icon: 'check', title: 'Save', type: 'primary'}}
          showInvalidField={showInvalidField}
        />
        <ConfirmModal
          isOpen={isOpenConfirm}
          onCloseModal={this.onCloseConfirmModal}
          removeItem={this.removeItem}/>
        <AlertNotify alertInfo={alertNotifyInfo}/>
      </>
    );
  }
}

const mapStateToProps = (
  {
    collection,
    filter,
    paging,
  }) => ({
  ...getCollection(collection, paging, filter),
  paging,
  filter,
});

const mapDispatchToProps = (dispatch) => ({
  toggleFavorite: (id) => dispatch(toggleFavoriteCollectionItem((id))),
  removeItem: (id) => dispatch(removeCollectionItem(id)),
  updateItemToCollection: (item) => dispatch(updateCollectionItem(item.id, item)),
  gotoPage: (page) => dispatch(gotoPage(page)),
});

export default connect(
  mapStateToProps,
  mapDispatchToProps,
)(CollectionContainer);
